	module.exports = {"data":{"post":{"html":"<p>In the very strange but true department: in front of our office today about 30 demonstrators protested against the Internet. The protest was aimed at the main tenants of our building, AOL Germany and Bertelsmann Online, with signs reading 'Where is our Internet' and chants of 'Wir sind das Netz' and 'Ihr seid drin, wir sind draußen' (a play on AOL Germany's ad campaign with Boris Becker, 'Bin ich drin?'). The best sign was 'Ficken statt Klicken', which can only be paraphrased as 'Make Love instead of Clicks.'</p>\n<p>Since the protesters also carried over half a dozen video cameras, so I am guessing that the protest was staged as part of some student film project. But it was fun nontheless.</p>","fields":{"title":"Wir sind das Netz!","date":"30 Nov 2000","url":"https://papascott.de/2000-11-30-wir-sind-das-netz/"}}},"pathContext":{"slug":"/2000-11-30-wir-sind-das-netz/","prev":{"fields":{"title":"Flying away for the weekend","date":"28 Nov 2000","slug":"/2000-11-28-flying-away-for-the-weekend/"}},"next":{"fields":{"title":"Weekend Trip","date":"01 Dec 2000","slug":"/2000-12-01-weekend-trip/"}}}}

